import React, {ChangeEvent, useState} from 'react';
import s from './Settings.module.css';
import DoneIcon from '@mui/icons-material/Done';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import IconButton from "@mui/material/IconButton";
import {SuperInput} from "../../universal/SuperInput/SuperInput";


type SettingsPropsType = {
    startCount: number
    maxCount: number
    stepCount: number
    addNewSettings: (newStartValue: number, newMaxValue: number, newStepValue: number) => void
    changeDisplay: () => void
}

export const Settings = (props: SettingsPropsType) => {
    const {startCount, maxCount, stepCount, addNewSettings, changeDisplay} = props;
    //    useEffect  ,        
    const [start, setStar] = useState<number>(startCount);
    const [max, setMax] = useState<number>(maxCount);
    const [step, setStep] = useState<number>(stepCount);

    //     
    const onChangeStartHandler = (e: ChangeEvent<HTMLInputElement>): void => {
        setStar(+e.currentTarget.value);
    }
    const onChangeMaxHandler = (e: ChangeEvent<HTMLInputElement>): void => {
        setMax(+e.currentTarget.value);
    }
    const onChangeStepHandler = (e: ChangeEvent<HTMLInputElement>): void => {
        setStep(+e.currentTarget.value);
    }

    //         
    const saveSetting = (): void => {
        addNewSettings(start, max, step);
    }

    const error = start === max || start > max;
    const errorStart = error || start < 0;
    const errorMax = error || max < 0;
    const errorStep = step <= 0;

    const disabled = errorStart || errorMax || errorStep;
    const colorResetButton = disabled ? "inherit" : "primary";


    return (
        <>
            <div className={s.control}>
                <IconButton
                    onClick={changeDisplay}
                    color={colorResetButton}
                >
                    <ArrowBackIosIcon/>
                </IconButton>
                <div><b>Settings</b></div>
                <IconButton
                    onClick={saveSetting}
                    disabled={disabled}
                    color={colorResetButton}
                >
                    <DoneIcon/>
                </IconButton>
            </div>
            <div className={s.settings}>
                <SuperInput
                    title="START VALUE"
                    onChange={onChangeStartHandler}
                    setValue={setStar}
                    value={start}
                    error={errorStart}
                />
                <SuperInput
                    title="MAX VALUE"
                    onChange={onChangeMaxHandler}
                    setValue={setMax}
                    value={max}
                    error={errorMax}
                />
                <SuperInput
                    title="STEP"
                    onChange={onChangeStepHandler}
                    setValue={setStep}
                    value={step}
                    error={errorStep}
                />
            </div>
        </>
    )
}